.image-gallery {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-auto-rows: 400px;
  grid-gap: 40px;
  margin-bottom: 40px;
}

.image-gallery-header {
  margin-top: 50px;
  margin-bottom: 40px;
}

.image-gallery img {
  width: 100%;
}



