.newtask {
  height: 8vh;
  border: 1px solid #DDD;
  border-radius: 4px;
  margin-bottom: 1vh;
  padding-left: 20px;
  font-size: 2rem;
  background: #e2ece2;
  display: grid;
  grid-template-columns: 4fr 1fr;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.newtask p {
  margin-bottom: 0;
}

.newproject:hover {
  background: #e4ece4;
}

.checkmark {
  opacity: 0.5;
  padding-left: 3rem;
}

.checkmark:hover{
  opacity: 1;
}



